import gridDraw from '../components/gridDraw/gridDraw';
import { globals, gameState } from '../globals/globals';
import returnToPlayScreen from './returnToPlayScreen';

import { startTimer } from './timer';
import { windowOpen, windowClose } from './windowRemote';

export default function startNextPuzzle() {
  const board = document.querySelector('.board');
  const stopButton = document.querySelector('.play-screen__menu-button');
  const timer = document.querySelector('.timer');
  const menuContinue = document.querySelector('.menu__continue');
  const menuContinueNext = document.querySelector('.menu__continue-next');
  const solvedWindow = document.querySelector('.solved');

  gameState.isNewGame = false;
  returnToPlayScreen();
  if (gameState.currentPuzzleNumber === globals.puzzlesData.length - 1) {
    gameState.currentPuzzleNumber = 0;
  } else gameState.currentPuzzleNumber += 1;

  const grid = gridDraw();

  menuContinue.classList.remove('hide');
  menuContinueNext.classList.add('hide');

  stopButton.classList.remove('hide');
  timer.classList.remove('hide');
  gameState.isInProgress = true;

  board.appendChild(grid);
  windowClose(solvedWindow);

  setTimeout(() => {
    windowOpen(board);
    startTimer(0);
  }, 300);
}
