import boardDraw from '../boardDraw/boardDraw';
import createDivWithClass from '../../methods/createDivWithClass';
import createTimer from '../createTimer/createTimer';
import createButton from '../createButton/createButton';
import createSolvedWindow from '../createSolvedWindow/createSolvedWindow';
import returnToMainMenu from '../../methods/returnToMainMenu';

import './playScreenDraw.css';
import { saveGame } from '../../methods/saveGame';

export default function playScreenDraw() {
  const playScreen = createDivWithClass('play-screen');
  const timer = createTimer();
  const menuButton = createButton('Stop game');
  const board = boardDraw();
  const gameOverScreen = createSolvedWindow();

  menuButton.addEventListener('mousedown', () => {
    const menuContinue = document.querySelector('.menu__continue');
    const menuContinueNext = document.querySelector('.menu__continue-next');

    menuContinue.classList.remove('hide');
    menuContinueNext.classList.add('hide');
    saveGame();
    returnToMainMenu();
  });

  menuButton.classList.add('play-screen__menu-button');

  playScreen.appendChild(timer);
  playScreen.appendChild(menuButton);
  playScreen.appendChild(board);
  playScreen.appendChild(gameOverScreen);

  return playScreen;
}
