import createDivWithClass from '../../methods/createDivWithClass';
import createWindow from '../createWindow/createWindow';
import createButton from '../createButton/createButton';
import returnToMainMenu from '../../methods/returnToMainMenu';
import startNextPuzzle from '../../methods/startNextPuzzle';

import { windowOpen, windowClose } from '../../methods/windowRemote';

import './createSolvedWindow.css';

const solvedWindow = createWindow('solved');

export default function createSolvedWindow() {
  const solvedHeading = createDivWithClass('solved__heading');
  const solvedMessage = createDivWithClass('solved__message');
  const buttonsContainer = createDivWithClass('buttons-container');
  const solvedMenuButton = createButton('Open menu');
  const solvedNextButton = createButton('Next puzzle');

  solvedMenuButton.classList.add('solved__button');
  solvedNextButton.classList.add('solved__button');
  solvedHeading.innerText = 'Congratulations!';
  solvedWindow.appendChild(solvedHeading);
  solvedWindow.appendChild(solvedMessage);
  solvedWindow.appendChild(buttonsContainer);
  buttonsContainer.appendChild(solvedMenuButton);
  buttonsContainer.appendChild(solvedNextButton);
  solvedMenuButton.addEventListener('mousedown', closeSolvedWindow);
  solvedNextButton.addEventListener('mousedown', startNextPuzzle);

  return solvedWindow;
}

export function openSolvedWindow() {
  windowOpen(solvedWindow);
}

export function closeSolvedWindow() {
  const menuContinue = document.querySelector('.menu__continue');
  const menuContinueNext = document.querySelector('.menu__continue-next');

  menuContinue.classList.add('hide');
  menuContinueNext.classList.remove('hide');
  windowClose(solvedWindow);
  returnToMainMenu();
}
