import { gameState } from '../globals/globals';

export default function loadGame() {
  const state = JSON.parse(localStorage.getItem('gameState'));

  if (state) {
    gameState.isInProgress = state.isInProgress;
    gameState.isNewGame = state.isNewGame;
    gameState.lastTime = state.lastTime;
    gameState.currentPuzzleNumber = state.currentPuzzleNumber;
    gameState.currentDeskState = state.currentDeskState;
  }
}
