import checkState from './checkState';

export default function createDivWithClass(className, textContent, state) {
  const element = document.createElement('div');
  element.className = className;
  if (textContent) {
    element.textContent = textContent;
  }
  if (state) {
    element.setAttribute('data-orig-state', state);
    element.addEventListener('mousedown', checkState);
  }
  return element;
}
