import './index.css';
import createMainPage from './modules/components/createMainPage/createMainPage';
import { globals } from './modules/globals/globals';
import loadProgress from './modules/methods/loadProgress';

async function getData() {
  try {
    const response = await fetch('./data/data.json');
    if (!response.ok) throw new Error(`LOADING ERROR: ${response.status}`);
    return await response.json();
  } catch {
    console.error(error);
    return null;
  }
}

async function init() {
  const data = await getData();
  if (data) {
    globals.puzzlesData = data;
    loadProgress();
    createMainPage();
  }
}

init();
