import gridDraw from '../components/gridDraw/gridDraw';
import { gameProgress, gameState } from '../globals/globals';
import returnToPlayScreen from './returnToPlayScreen';
import saveProgress from './saveProgress';
import { startTimer } from './timer';
import { windowOpen } from './windowRemote';

export default function startNewGame() {
  const mainPage = document.querySelector('.main');
  const menuContinue = document.querySelector('.menu__continue');
  const board = document.querySelector('.board');
  const stopButton = document.querySelector('.play-screen__menu-button');
  const timer = document.querySelector('.timer');

  let grid = document.querySelector('.grid');

  gameState.currentPuzzleNumber = 0;
  gameState.currentDeskState = [];
  gameState.lastTime = 0;
  gameState.isNewGame = true;

  gameProgress.length = 0;
  saveProgress();

  if (grid) {
    board.removeChild(grid);
  }

  grid = gridDraw();
  board.appendChild(grid);
  stopButton.classList.remove('hide');
  timer.classList.remove('hide');
  gameState.isInProgress = true;
  menuContinue.classList.remove('hide');
  returnToPlayScreen();

  startTimer(0);
  windowOpen(board);
}
