import gridDraw from '../components/gridDraw/gridDraw';
import { gameState } from '../globals/globals';
import returnToPlayScreen from './returnToPlayScreen';
import { getTimerValue, startTimer } from './timer';

export default function continueGame() {
  const board = document.querySelector('.board');
  const time = getTimerValue(true);
  const deskState = document.querySelectorAll('.desk-cell');

  if (deskState.length === 0) {
    const grid = gridDraw();

    board.appendChild(grid);
  }

  returnToPlayScreen();

  if (time) {
    startTimer(time);
  } else {
    startTimer(gameState.lastTime);
  }
}
