import cluesCalc from '../../methods/cluesCalc';
import createDivWithClass from '../../methods/createDivWithClass';
import { globals, gameState } from '../../globals/globals';

import './gridDraw.css';

export default function gridDraw() {
  const currentGrid = globals.puzzlesData[gameState.currentPuzzleNumber].grid;
  const currentGridRows = currentGrid.length;
  const currentGridCols = currentGrid[0].length;
  const clues = cluesCalc(currentGrid);
  const grid = createDivWithClass('grid');

  document.documentElement.style.setProperty(
    '--grid-size',
    currentGridCols.toString()
  );
  for (let row = 0; row < currentGridRows + 1; row++) {
    for (let col = 0; col < currentGridCols + 1; col++) {
      let state;
      let cell;
      let cellClass = 'cell';
      let clueItems = null;

      if (row === 0 && col === 0) {
        cellClass += ' zero-cell';
      } else if (row === 0 && col > 0) {
        cellClass += ' col-clue';
        clueItems = clues.cols[col - 1];
      } else if (col === 0 && row > 0) {
        cellClass += ' row-clue';
        clueItems = clues.rows[row - 1];
      } else if (row > 0 && col > 0) {
        if (
          gameState.isInProgress &&
          gameState.currentDeskState.length > 0 &&
          gameState.currentDeskState[row - 1][col - 1] === 1
        ) {
          cellClass += ' checked';
        }
        cellClass += ' desk-cell';
        state = currentGrid[row - 1][col - 1].toString();
      }

      if (col === 5) cellClass += ' fifth-col';
      if (row === 5) cellClass += ' fifth-row';

      cell = createDivWithClass(cellClass, null, state);

      if (clueItems) {
        clueItems.forEach((item) => {
          const clue = createDivWithClass('clue-text', item);
          cell.appendChild(clue);
        });
      }

      grid.appendChild(cell);
    }
  }

  return grid;
}
