import createDivWithClass from '../../methods/createDivWithClass';

import './createRemoteSlider.css';

function createSliderElement() {
  const slider = createDivWithClass('slider');
  const thumb = createDivWithClass('slider-thumb');

  slider.appendChild(thumb);

  return { slider, thumb };
}

function initSlider(slider, thumb, callback) {
  let value;
  let shiftX;

  const moveHandler = function (event) {
    event.preventDefault();

    const clientX = event.touches ? event.touches[0].clientX : event.clientX;
    const rightEdge = slider.offsetWidth - thumb.offsetWidth;

    let newLeft = clientX - shiftX - slider.getBoundingClientRect().left;

    if (newLeft < 0) {
      newLeft = 0;
    }

    if (newLeft > rightEdge) {
      newLeft = rightEdge;
    }

    thumb.style.left = newLeft + 'px';
    value = newLeft / rightEdge;

    callback(value);
  };

  const upHandler = function () {
    callback(value);
    document.removeEventListener('mouseup', upHandler);
    document.removeEventListener('mousemove', moveHandler);
    document.removeEventListener('touchend', upHandler);
    document.removeEventListener('touchmove', moveHandler);
  };

  const downHandler = function (event) {
    event.preventDefault();

    const clientX = event.touches ? event.touches[0].clientX : event.clientX;
    shiftX = clientX - thumb.getBoundingClientRect().left;

    document.addEventListener('mousemove', moveHandler);
    document.addEventListener('mouseup', upHandler);
    document.addEventListener('touchmove', moveHandler);
    document.addEventListener('touchend', upHandler);
  };

  thumb.addEventListener('mousedown', downHandler);
  thumb.addEventListener('touchstart', downHandler);

  thumb.ondragstart = function () {
    return false;
  };

  slider.addEventListener('mousedown', function (event) {
    event.preventDefault();

    const thumbHalfWidth = thumb.offsetWidth / 2;
    const rightEdge = slider.offsetWidth - thumbHalfWidth;

    let newLeft =
      event.clientX - slider.getBoundingClientRect().left - thumbHalfWidth;

    if (newLeft < 0) {
      newLeft = 0;
    }
    if (newLeft > rightEdge) {
      newLeft = rightEdge;
    }

    thumb.style.left = newLeft + 'px';

    value = newLeft / rightEdge;

    callback(value); // Вызываем callback с новым значением

    // Непосредственно вызываем обработчик события mousedown для ползунка
    downHandler(event);
  });

  slider.addEventListener('touchstart', function (event) {
    event.preventDefault();

    const thumbHalfWidth = thumb.offsetWidth / 2;
    const rightEdge = slider.offsetWidth - thumbHalfWidth;

    let newLeft =
      event.touches[0].clientX -
      slider.getBoundingClientRect().left -
      thumbHalfWidth;

    if (newLeft < 0) {
      newLeft = 0;
    }
    if (newLeft > rightEdge) {
      newLeft = rightEdge;
    }

    thumb.style.left = newLeft + 'px';

    value = newLeft / rightEdge;

    callback(value); // Вызываем callback с новым значением

    // Непосредственно вызываем обработчик события touchstart для ползунка
    downHandler(event);
  });
}

export function createRemoteSlider(header, callback) {
  const sliderContainer = createDivWithClass('slider-container');
  const sliderText = createDivWithClass('slider__text');
  const { slider, thumb } = createSliderElement(header);

  initSlider(slider, thumb, callback);

  sliderText.innerText = header;
  sliderContainer.appendChild(sliderText);
  sliderContainer.appendChild(slider);

  return sliderContainer;
}

export function getSliderValue(element) {
  const thumb = element.querySelector('.slider-thumb');
  const slider = element.querySelector('.slider');

  const sliderWidth = slider.offsetWidth - thumb.offsetWidth;
  const thumbLeft = parseInt(thumb.style.left, 10);
  const valueInPercent = (thumbLeft / sliderWidth) * 100;

  return valueInPercent;
}

export function setSliderValue(sliderContainer, value) {
  const slider = sliderContainer.querySelector('.slider');
  const thumb = sliderContainer.querySelector('.slider-thumb');
  const rightEdge = slider.offsetWidth - thumb.offsetWidth;
  const newValue = (rightEdge / 100) * (value * 100);

  thumb.style.left = newValue + 'px';
}
