import { setSliderValue } from '../components/createRemoteSlider/createRemoteSlider';
import { gameSettings } from '../globals/globals';
import closeMenuWindow from './closeMenuWindow';
import { windowOpen } from './windowRemote';

export default function openSettings() {
  const settingsWindow = document.querySelector('.settings');
  const musicVolumeSlider = document.querySelector('.volume');
  const soundVolumeSlider = document.querySelector('.sound');
  setSliderValue(musicVolumeSlider, gameSettings.musicVolume);
  setSliderValue(soundVolumeSlider, gameSettings.soundsVolume);

  closeMenuWindow();
  setTimeout(() => {
    windowOpen(settingsWindow);
  }, 300);
}
