import createDivWithClass from '../../methods/createDivWithClass';

import './createMenuItem.css';

export default function createMenuItem(itemName) {
  const menuItem = createDivWithClass('menu-item');
  const menuItemText = createDivWithClass('menu-item__text');

  menuItemText.innerText = itemName;
  menuItem.appendChild(menuItemText);

  return menuItem;
}
