import { openSolvedWindow } from '../components/createSolvedWindow/createSolvedWindow';
import { clearTimer, getTimerValue } from './timer';
import { gameProgress, gameState } from '../globals/globals';
import { saveGame } from './saveGame';

import isWin from './isWin';
import { clickPlay } from './soundsRemote';
import saveProgress from './saveProgress';

export default function checkState(event) {
  const element = event.target;
  const isMarked = element.classList.contains('marked');
  const isChecked = element.classList.contains('checked');

  if (!gameState.isInProgress) return;
  clickPlay();

  switch (event.button) {
    case 0:
      element.classList.remove('marked', 'checked');
      if (!isChecked && !isMarked) {
        element.classList.add('checked');
      }
      break;
    case 2:
      element.classList.remove('marked', 'checked');
      if (!isChecked && !isMarked) {
        element.classList.add('marked');
      }
      break;
  }

  if (isWin()) {
    const grid = document.querySelector('.grid');
    const board = document.querySelector('.board');
    const solvedTime = document.querySelector('.solved__message');
    const stopButton = document.querySelector('.play-screen__menu-button');
    const timer = document.querySelector('.timer');
    const time = getTimerValue();
    const checked = document.querySelectorAll('.checked');

    const gameProgressItem = {};

    gameProgressItem.puzzleNumber = gameState.currentPuzzleNumber;
    gameProgressItem.puzzleTime = getTimerValue(true);
    gameProgress.push(gameProgressItem);
    saveProgress();

    console.table(gameProgress);

    gameState.lastTime = getTimerValue(true);
    gameState.isInProgress = false;
    saveGame();

    stopButton.classList.add('hide');
    timer.classList.add('hide');
    solvedTime.innerText = `You’ve successfully  \nsolved the puzzle!  \nYour time: ${time}`;
    checked.forEach((element) => {
      element.classList.add('flash');
    });

    clearTimer();
    setTimeout(() => {
      board.classList.remove('show');
      board.classList.add('hide');
      setTimeout(() => {
        board.removeChild(grid);
        openSolvedWindow();
      }, 300);
    }, 3000);
  }
}
