export default function isWin() {
  const deskState = document.querySelectorAll('.desk-cell');

  for (let pos = 0; pos < deskState.length; pos += 1) {
    const element = deskState[pos];

    if (
      element.getAttribute('data-orig-state') === '0' &&
      element.classList.contains('checked')
    ) {
      return false;
    }
    if (
      element.getAttribute('data-orig-state') === '1' &&
      (!element.classList.contains('checked') ||
        element.classList.contains('marked'))
    ) {
      return false;
    }
  }
  return true;
}
