import createProgressItem from '../components/createProgressItem/createProgressItem';
import { gameProgress } from '../globals/globals';
import closeMenuWindow from './closeMenuWindow';
import { convertSecondsToMMSS } from './timer';
import { windowOpen } from './windowRemote';

export default function openProgress() {
  const progressWindow = document.querySelector('.progress');
  const progressContainer = progressWindow.querySelector(
    '.progress__container'
  );

  while (progressContainer.childNodes.length > 1) {
    progressContainer.removeChild(progressContainer.lastChild);
  }

  gameProgress.forEach((element) => {
    const puzzleNumber = element.puzzleNumber;
    const puzzleTime = convertSecondsToMMSS(element.puzzleTime);
    const progressItem = createProgressItem(puzzleNumber, puzzleTime);
    progressContainer.appendChild(progressItem);
  });

  closeMenuWindow();
  setTimeout(() => {
    windowOpen(progressWindow);
  }, 300);
}
