export default function cluesCalc(currentGrid) {
  const clues = {};

  clues.rows = getRowClues(currentGrid);
  clues.cols = getColClues(currentGrid);

  return clues;
}

function getRowClues(currentGrid) {
  const len = currentGrid.length;

  return currentGrid.map((item) => {
    const currentRow = [];
    let count = 0;

    for (let col = 0; col < len; col += 1) {
      if (item[col] > 0) {
        count += item[col];
      }

      if (item[col] === 0 && count > 0) {
        currentRow.push(count);
        count = 0;
      }
    }

    if (count > 0) {
      currentRow.push(count);
    }

    return currentRow;
  });
}

function getColClues(currentGrid) {
  const lenCol = currentGrid[0].length;
  const lenRow = currentGrid.length;
  const result = [];

  for (let col = 0; col < lenCol; col += 1) {
    const currentCol = [];
    let count = 0;

    for (let row = 0; row < lenRow; row += 1) {
      if (currentGrid[row][col] > 0) {
        count += currentGrid[row][col];
      }

      if (currentGrid[row][col] === 0 && count > 0) {
        currentCol.push(count);
        count = 0;
      }
    }

    if (count > 0) {
      currentCol.push(count);
    }

    result.push(currentCol);
  }

  return result;
}
