import { windowClose, windowOpen } from './windowRemote';
import { gameSettings } from '../globals/globals';
import saveSettings from './saveSettings';

export default function closeSettings() {
  const settingsWindow = document.querySelector('.settings');
  const menuWindow = document.querySelector('.menu');

  saveSettings();

  windowClose(settingsWindow);
  setTimeout(() => {
    windowOpen(menuWindow);
  }, 300);
}
