import createDivWithClass from '../../methods/createDivWithClass';
import loadSettings from '../../methods/loadSettings';
import startMain from '../../methods/startMain';

import './createIntroScreen.css';

export default function createIntroScreen() {
  const introScreen = createDivWithClass('intro-screen');
  const introScreenStart = createDivWithClass('intro-screen__start-button');

  introScreen.appendChild(introScreenStart);

  introScreenStart.innerText = 'START';
  loadSettings();

  introScreenStart.addEventListener('click', startMain);

  return introScreen;
}
