import createIntroScreen from '../createIntroScreen/createIntroScreen';
import createMenu from '../createMenu/createMenu';
import playScreenDraw from '../playScreenDraw/playScreenDraw';
import createDivWithClass from '../../methods/createDivWithClass';
import createFooter from '../createFooter/createFooter';

import './createMainPage.css';

export default function createMainPage() {
  const body = document.querySelector('.body');
  const introScreen = createIntroScreen();
  const mainPage = createDivWithClass('main');
  const menu = createMenu();
  const playScreen = playScreenDraw();
  const footer = createFooter();

  body.appendChild(introScreen);
  body.appendChild(mainPage);

  mainPage.appendChild(menu);
  mainPage.appendChild(playScreen);
  body.appendChild(footer);
}
