import createDivWithClass from '../../methods/createDivWithClass';

import './createProgressItem.css';

export default function createProgressItem(puzzleNumber, puzzleTime) {
  const progressItem = createDivWithClass('progress__item');
  const puzzleNumberElement = createDivWithClass('progress__puzzle-number');
  const puzzleTimeElement = createDivWithClass('progress__puzzle-time');

  puzzleNumberElement.innerText = puzzleNumber;
  puzzleTimeElement.innerText = puzzleTime;

  progressItem.appendChild(puzzleNumberElement);
  progressItem.appendChild(puzzleTimeElement);

  return progressItem;
}
