import getGridSize from './getGridSize';

import { gameProgress, gameState } from '../globals/globals';
import { getTimerValue } from './timer';

export function saveGame() {
  const deskState = document.querySelectorAll('.desk-cell');
  const currentDesk = [];
  const gridSize = getGridSize();

  let count = 0;

  for (let row = 0; row < gridSize.rows; row += 1) {
    const colArray = [];
    for (let col = 0; col < gridSize.cols; col += 1) {
      if (deskState[count].classList.contains('checked')) {
        colArray.push(1);
      } else {
        colArray.push(0);
      }
      count += 1;
    }
    currentDesk.push(colArray);
  }

  gameState.isNewGame = false;
  gameState.lastTime = getTimerValue(true);
  gameState.currentDeskState = gameState.isInProgress ? currentDesk : [];
  localStorage.setItem('gameState', JSON.stringify(gameState));
}
