import createDivWithClass from '../../methods/createDivWithClass';

import './createButton.css';

export default function createButton(text, callback) {
  const button = createDivWithClass(`button`);
  const buttonText = createDivWithClass(`button__text`);

  button.appendChild(buttonText);
  buttonText.innerText = text;
  if (callback) button.addEventListener('mousedown', callback);

  return button;
}
