import createDivWithClass from '../../methods/createDivWithClass';
import createMenuItem from '../createMenuItem/createMenuItem';
import createWindow from '../createWindow/createWindow';
import loadGame from '../../methods/loadGame';
import startNextPuzzle from '../../methods/startNextPuzzle';
import startNewGame from '../../methods/startNewGame';
import continueGame from '../../methods/continueGame';

import { windowOpen } from '../../methods/windowRemote';
import { gameState } from '../../globals/globals';

import './createMenu.css';
import openSettings from '../../methods/openSettings';
import createSettingsMenu from '../createSettingsMenu/createSettingsMenu';
import createProgressWindow from '../createProgressWindow/createProgressWindow';
import openProgress from '../../methods/openProgress';

export default function createMenu() {
  const menu = createDivWithClass('menu__screen');
  const menuWindow = createWindow('menu');
  const titleContainer = createDivWithClass('title__container');
  const titleLogo = createDivWithClass('title__logo');
  const titleText = createDivWithClass('title__text');
  const menuStart = createMenuItem('Start new game');
  const menuContinue = createMenuItem('Continue game');
  const menuContinueNext = createMenuItem('Next puzzle');
  const menuProgress = createMenuItem('Show progress');
  const menuSettings = createMenuItem('Settings');
  const settingsWindow = createSettingsMenu();
  const progressWindow = createProgressWindow();

  loadGame();

  menuContinue.classList.add('hide');
  menuContinueNext.classList.add('hide');

  if (gameState.isInProgress) {
    menuContinue.classList.remove('hide');
  } else if (!gameState.isNewGame) {
    menuContinueNext.classList.remove('hide');
  }

  menuContinue.classList.add('menu__continue');
  menuContinueNext.classList.add('menu__continue-next');

  titleText.innerText = 'Nonograms';

  menu.appendChild(menuWindow);
  menuWindow.appendChild(titleContainer);
  titleContainer.appendChild(titleLogo);
  titleContainer.appendChild(titleText);
  menuWindow.appendChild(menuStart);
  menuWindow.appendChild(menuContinue);
  menuWindow.appendChild(menuContinueNext);
  menuWindow.appendChild(menuProgress);
  menuWindow.appendChild(menuSettings);
  menu.appendChild(settingsWindow);
  menu.appendChild(progressWindow);

  menuStart.addEventListener('mousedown', startNewGame);
  menuContinue.addEventListener('mousedown', continueGame);
  menuContinueNext.addEventListener('mousedown', startNextPuzzle);
  menuProgress.addEventListener('mousedown', openProgress);
  menuSettings.addEventListener('mousedown', openSettings);

  return menu;
}
