let timerID;
let timerValue;

export function startTimer(timerStartValue) {
  timerValue = timerStartValue;
  if (!timerID) {
    setCurrentTime(timerValue);
    timerID = setInterval(() => {
      setCurrentTime(timerValue);
      timerValue += 1;
    }, 1000);
  }
}

export function clearTimer() {
  clearInterval(timerID);
  timerID = null;
}

export function getTimerValue(inSec) {
  if (inSec) return timerValue - 1;
  return convertSecondsToMMSS(timerValue - 1);
}

function setCurrentTime(seconds) {
  const time = convertSecondsToMMSS(seconds);
  const timerText = document.querySelector('.timer__text');
  timerText.innerText = `time ${time}`;
}

export function convertSecondsToMMSS(seconds) {
  const date = new Date(null);
  date.setSeconds(seconds);
  const timeString = date.toISOString().slice(14, 19);

  return timeString;
}
