/* import { Howl } from 'howler'; */
import { gameSettings } from '../globals/globals';

let music;
let click;
let winOpen;
let winClose;
let scrollUp;
let scrollDown;

export function initializeAudio() {
  let musicTime = 0;
  return;
  music = new Howl({
    src: [require('../../assets/sounds/music.ogg')],
    autoplay: false,
    loop: true,
    volume: 0,
    html5: true,
  });

  click = new Howl({
    src: [require('../../assets/sounds/click_square.ogg')],
    autoplay: false,
    volume: 0,
    html5: true,
  });

  winOpen = new Howl({
    src: [require('../../assets/sounds/window_open.ogg')],
    autoplay: false,
    volume: 0,
    html5: true,
  });

  winClose = new Howl({
    src: [require('../../assets/sounds/window_close.ogg')],
    autoplay: false,
    volume: 0,
    html5: true,
  });

  scrollUp = new Howl({
    src: [require('../../assets/sounds/scroll_up.ogg')],
    autoplay: false,
    volume: 0,
    html5: true,
  });

  scrollDown = new Howl({
    src: [require('../../assets/sounds/scroll_down.ogg')],
    autoplay: false,
    volume: 0,
    html5: true,
  });

  window.addEventListener('blur', function () {
    musicTime = music.seek();
    music.pause();
  });

  window.addEventListener('focus', function () {
    const introScreen = document.querySelector('.intro-screen');
    if (introScreen.classList.contains('hide')) {
      music.seek(musicTime);
      music.play();
    }
  });
}

export function musicStart() {
  return;
  music.play();
}

export function musicStop() {
  return;
  music.stop();
}

export function clickPlay() {
  return;
  click.play();
}

export function winOpenPlay() {
  return;
  winOpen.play();
}

export function winClosePlay() {
  return;
  winClose.play();
}

export function scrollUpPlay() {
  return;
  scrollUp.play();
}

export function scrollDownPlay() {
  return;
  scrollDown.play();
}

export function musicVolumeSet(value) {
  /*   music.volume(value); */
  gameSettings.musicVolume = value;
}

export function soundsVolumeSet(value) {
  /*   click.volume(value);
  winOpen.volume(value);
  winClose.volume(value);
  scrollUp.volume(value);
  scrollDown.volume(value); */
  gameSettings.soundsVolume = value;
}
