import { gameSettings } from '../globals/globals';
import {
  initializeAudio,
  musicStart,
  musicVolumeSet,
  soundsVolumeSet,
} from './soundsRemote';
import { windowOpen } from './windowRemote';

export default function startMain() {
  const elem = document.documentElement;
  const introScreen = document.querySelector('.intro-screen');
  const menuWindow = document.querySelector('.menu');

  introScreen.classList.add('hide');

  if (document.fullscreenElement) {
    document.exitFullscreen();
  } else {
    elem.requestFullscreen();
  }

  initializeAudio();
  musicVolumeSet(gameSettings.musicVolume);
  soundsVolumeSet(gameSettings.soundsVolume);
  musicStart();
  windowOpen(menuWindow);
}
