import createDivWithClass from '../../methods/createDivWithClass';
import createMenuItem from '../createMenuItem/createMenuItem';
import createWindow from '../createWindow/createWindow';
import closeSettings from '../../methods/closeSettings';
import { createRemoteSlider } from '../createRemoteSlider/createRemoteSlider';
import { musicVolumeSet, soundsVolumeSet } from '../../methods/soundsRemote';

import './createSettingsMenu.css';

export default function createSettingsMenu() {
  const settingsWindow = createWindow('settings');
  const titleText = createDivWithClass('title__text');
  const volumeMusicSlider = createRemoteSlider('MUSIC:', musicVolumeSet);
  const volumeSoundSlider = createRemoteSlider('SOUND:', soundsVolumeSet);
  const menuReturn = createMenuItem('Return to menu');

  volumeMusicSlider.classList.add('volume');
  volumeSoundSlider.classList.add('sound');
  titleText.innerText = 'SETTINGS';

  settingsWindow.appendChild(titleText);
  settingsWindow.appendChild(volumeMusicSlider);
  settingsWindow.appendChild(volumeSoundSlider);
  settingsWindow.appendChild(menuReturn);

  menuReturn.addEventListener('mousedown', closeSettings);

  return settingsWindow;
}
