import closeProgress from '../../methods/closeProgress';
import createDivWithClass from '../../methods/createDivWithClass';
import createMenuItem from '../createMenuItem/createMenuItem';
import createProgressItem from '../createProgressItem/createProgressItem';
import createWindow from '../createWindow/createWindow';

import './createProgressWindow.css';

export default function createProgressWindow() {
  const progressWindow = createWindow('progress');
  const titleText = createDivWithClass('title__text');
  const progressContainer = createDivWithClass('progress__container');
  const menuReturn = createMenuItem('Return to menu');
  const progressHeader = createProgressItem('Puzzle number', 'Time spent');

  titleText.innerText = 'PROGRESS';

  progressWindow.appendChild(titleText);
  progressWindow.appendChild(progressContainer);
  progressWindow.appendChild(menuReturn);

  progressContainer.appendChild(progressHeader);

  menuReturn.addEventListener('mousedown', closeProgress);

  return progressWindow;
}
