import createDivWithClass from '../../methods/createDivWithClass';

import './createFooter.css';
export default function createFooter() {
  const footer = createDivWithClass('footer');
  const footerText = createDivWithClass('footer__text');
  const footerLogo = createDivWithClass('footer__logo');

  footerText.innerText = '© 2024 MONOPlan group';
  footer.appendChild(footerText);
  footer.appendChild(footerLogo);

  return footer;
}
