import { gameSettings } from '../globals/globals';

export default function loadSettings() {
  const settings = JSON.parse(localStorage.getItem('gameSettings'));

  if (settings) {
    gameSettings.musicVolume = settings.musicVolume;
    gameSettings.soundsVolume = settings.soundsVolume;
    gameSettings.fullscreen = settings.fullscreen;
    gameSettings.language = settings.language;
  }
}
